import {
  NearBindgen,
  call,
  view,
  initialize,
  NearPromise,
  bytes,
  near,
} from "near-sdk-js";
import { promiseResult } from "near-sdk-js/lib/api";
import { AccountId, ONE_TERA_GAS } from "near-sdk-js/lib/types";
import { assert, deserialize, serialize } from "near-sdk-js/lib/utils";
import { FungibleToken } from "./fungible-token";

// 50000000000000
// 75000000000000
const ONE_TGAS = BigInt("10000000000000");
const FIVE_TGAS = BigInt("50000000000000");
const SEVEN_TEEN_TGAS = BigInt("170000000000000");
const SEVEN_TGAS = BigInt("70000000000000");
const STORAGE_DEPOSIT = BigInt("12500000000000000000000");
const NO_DEPOSIT = BigInt(0);
const ONE_YOCTO = BigInt("1");

const XCC_GAS = 10n ** 13n;

@NearBindgen({ requireInit: true })
export class IndicesToken extends FungibleToken {
  ref_finance: AccountId = "ref-finance.testnet";
  wrap_testnet: AccountId = "wrap.testnet";
  banana_testnet: AccountId = "banana.ft-fin.testnet";
  constructor() {
    super();
  }

  @initialize({})
  init({
    owner_id,
    total_supply,
  }: {
    owner_id: string;
    total_supply: string;
  }): void {
    return super.init({ owner_id, total_supply });
  }

  @call({ payableFunction: true })
  storage_deposit({ account_id }: { account_id: string }) {
    return super.storage_deposit({ account_id });
  }

  @call({ payableFunction: true })
  ft_transfer({
    receiver_id,
    amount,
    memo,
  }: {
    receiver_id: string;
    amount: string;
    memo: string;
  }): void {
    return super.ft_transfer({ receiver_id, amount, memo });
  }

  @call({ payableFunction: true })
  ft_transfer_call({
    receiver_id,
    amount,
    memo,
    msg,
  }: {
    receiver_id: string;
    amount: string;
    memo: string;
    msg: string;
  }): void {
    return super.ft_transfer_call({ receiver_id, amount, memo, msg });
  }

  @view({})
  ft_total_supply() {
    return super.ft_total_supply();
  }

  @view({})
  ft_balance_of({ account_id }: { account_id: string }): string {
    return super.ft_balance_of({ account_id });
  }

  @call({ privateFunction: true })
  withdraw_from_exchange({ address, poolId }): NearPromise {
    const tokenOut = deserialize(promiseResult(0)) as bigint;

    const tokenToBeReturned = tokenOut - BigInt(1);

    const promise = NearPromise.new(this.ref_finance)
      .functionCall(
        "withdraw",
        serialize({
          token_id: address,
          amount: "1",
        }),
        ONE_YOCTO,
        FIVE_TGAS
      )
      .then(
        NearPromise.new(this.ref_finance)
          .functionCall(
            "swap",
            bytes(
              JSON.stringify({
                actions: [
                  {
                    pool_id: poolId,
                    token_in: address,
                    amount_in: tokenToBeReturned.toString(),
                    token_out: this.wrap_testnet,
                    min_amount_out: "0",
                  },
                ],
              })
            ),
            ONE_YOCTO,
            FIVE_TGAS
          )
          .then(
            NearPromise.new(this.ref_finance).functionCall(
              "withdraw",
              serialize({
                token_id: this.wrap_testnet,
                amount: "1",
              }),
              ONE_YOCTO,
              FIVE_TGAS
            )
          )
      );

    return promise.asReturn();
  }

  // @call({ payableFunction: true })
  // buy_index_token({ swap_token_addresses, near_amounts, pool_ids }) {
  //   // for (let i = 0; i < swap_token_addresses.length; i++) {
  //   this.swap_token(
  //     swap_token_addresses[0],
  //     BigInt(near_amounts[0]),
  //     pool_ids[0]
  //   );
  //   // }
  // }

  @call({ payableFunction: true })
  buy_index_token({
    swap_token_addresses,
    near_amounts,
    pool_ids,
  }): NearPromise {
    const promise = NearPromise.new(this.wrap_testnet)
      .functionCall(
        "near_deposit",
        bytes("{}"),
        BigInt(near_amounts[0]) - ONE_YOCTO - ONE_YOCTO,
        FIVE_TGAS
      )
      .then(
        NearPromise.new(this.wrap_testnet).functionCall(
          "storage_deposit",
          bytes(JSON.stringify({ account_id: this.ref_finance })),
          STORAGE_DEPOSIT,
          FIVE_TGAS
        )
      )
      .then(
        NearPromise.new(this.wrap_testnet).functionCall(
          "ft_transfer_call",
          bytes(
            JSON.stringify({
              receiver_id: this.ref_finance,
              amount: (
                BigInt(near_amounts[0]) -
                ONE_YOCTO -
                ONE_YOCTO
              ).toString(),
              msg: "",
            })
          ),
          ONE_YOCTO,
          FIVE_TGAS
        )
      )
      .then(
        NearPromise.new(swap_token_addresses[0]).functionCall(
          "storage_deposit",
          bytes(JSON.stringify({ account_id: this.ref_finance })),
          STORAGE_DEPOSIT,
          FIVE_TGAS
        )
      )
      .then(
        NearPromise.new(this.ref_finance).functionCall(
          "swap",
          bytes(
            JSON.stringify({
              actions: [
                {
                  pool_id: pool_ids[0],
                  token_in: this.wrap_testnet,
                  amount_in: (
                    BigInt(near_amounts[0]) -
                    ONE_YOCTO -
                    ONE_YOCTO
                  ).toString(),
                  token_out: swap_token_addresses[0],
                  min_amount_out: "1",
                },
              ],
            })
          ),
          ONE_YOCTO,
          FIVE_TGAS
        )
      )
      .then(
        NearPromise.new(near.currentAccountId()).functionCall(
          "withdraw_from_exchange",
          bytes(
            JSON.stringify({
              address: swap_token_addresses[0],
              poolId: pool_ids[0],
            })
          ),
          NO_DEPOSIT,
          SEVEN_TEEN_TGAS
        )
      );

    return promise.asReturn();
  }
}

